body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo img {
  display: flex;
}

ul {
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

li:not(:last-child) {
  margin-right: 20px;
}

a {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  font-size: 12px;
  font-style: normal;
  line-height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.is-active, a:hover {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
